.movie{
    text-align: center;
    display: flex;
    padding :20px;
    justify-content: space-around;
    box-shadow: gray 0px 0px 10px 2px;
    margin: 50px 0;
}
.movie_data{
    float: left;
    width:100%;
    text-align: left;
    color:gray;
    font-family: 'Times New Roman', Times, serif;
}
.mov_title{
    text-align: center;
    font-weight: bold;
    color: rgb(39, 36, 36);
}
.genres {
    list-style-type: none;
    display: flex;
}
.genres > li {
    padding-right :5px;
}
.movie_sum{
    width: 500px;
}
.movie img{
    position: relative;
    width: 100%;
    height: auto;
    max-height: 350px;
    margin-right: 30px;
    top:0px;
    max-width: 200px;
    border: red 1px solid;
} 